@use 'base/variables' as v;
@use 'base/mixins' as m;

.list-comments {
    display: grid;
    gap: 2rem;

    @include m.tablet {
        @include m.grid(2, 2rem);
    }
    .container-div {
        transition-property: transform;
        transition-duration: 300ms;
        border-radius: 1rem;
        background-color: transparent;
        overflow: hidden;
        border: none;

        &:hover {
            transform: scale(1.05);
            cursor: pointer;
        }

        .container-info {
            display: flex;
            justify-content: space-between;
            align-items: center;
            background-color: #30343F;
            padding: .5rem 1rem;
    
            .user-name-comment {
                font-weight: v.$bold;
                color: #969696;
            }
            .date-comment {
                font-weight: v.$delgada;
                color: v.$blanco;
            }
        }

        .comment {
            text-align: center;
            padding: 1rem;
            background-color: v.$blanco;
            border-bottom-left-radius: 1rem;
            border-bottom-right-radius: 1rem;
            .comment-text {
                font-weight: v.$regular;
                color: v.$negro;
            }
            p {
                
                margin: 0 0 calc(v.$separacion / 4) 0;
            }
        }
    }
}

.user-comments {
    .container-all {
        margin-top: 5rem;
        @include m.tablet {
            @include m.grid(2, 5rem);
            align-items: center;
        }   

        .container-buttons {
            display: flex;
            flex-direction: column;
            justify-content: space-evenly;
            margin-top: 2rem;
            @include m.tablet {
                margin-top: 0;
            }
            
        }

        .boton {
            margin: 0;
        }

        .container-div {
            transition-property: transform;
            transition-duration: 300ms;
            border-radius: 1rem;
            background-color: transparent;
            overflow: hidden;
            border: none;
    
            &:hover {
                transform: scale(1.05);
                cursor: pointer;
            }
    
            .container-info {
                display: flex;
                justify-content: space-between;
                align-items: center;
                background-color: #30343F;
                padding: .5rem 1rem;
        
                .user-name-comment {
                    font-weight: v.$bold;
                    color: #969696;
                }
                .date-comment {
                    font-weight: v.$delgada;
                    color: v.$blanco;
                }
            }
    
            .comment {
                text-align: center;
                padding: 1rem;
                background-color: v.$blanco;
                border-bottom-left-radius: 1rem;
                border-bottom-right-radius: 1rem;
                .comment-text {
                    font-weight: v.$regular;
                    color: v.$negro;
                }
                p {
                    
                    margin: 0 0 calc(v.$separacion / 4) 0;
                }
            }
    }
    
    }
}

