@use 'base/variables' as v;
@use 'base/mixins' as m;

.boton {
    @include m.boton(v.$button-color, v.$blanco);
}

.button-delete {
    @include m.boton(#9b2226, v.$blanco);
}

.button-logout {
    @include m.boton(#9b2226, v.$blanco);
}

.button-pagers {
    @include m.boton(v.$button-pager, v.$blanco);
}

.button-login {
    @include m.boton(#702632, v.$blanco);
}

.button-create {
    @include m.boton(#40434E, v.$blanco);
}

.button-guest {
    @include m.boton(#080705, v.$blanco);
}

.button-comment {
    @include m.boton(v.$blanco, v.$negro);
}

.button-edit {
    @include m.boton(#1C3041, v.$blanco);
}

.button-change {
    @include m.boton(v.$button-pager, v.$blanco);
}

.button-crud {
    @include m.boton(#27474E, v.$blanco);
}

.button-crud {
    margin: 0;
}

.type-comments {
    margin: 5rem 0;
    @include m.tablet {
        display: flex;
        justify-content: space-between;
    }
    
    align-items: center;

    .button-comment {
        border: none;
        padding: 1rem 2rem;
        font-size: 1.8rem;
        border-radius: .5rem;
        font-weight: v.$regular;
        transition: all 300ms ease-in-out;

        &:hover {
            cursor: pointer;
            background-color: darken($color: v.$azul, $amount: 5%);
            color: v.$blanco;
        }

        &.current-comment {
            background-color: #468189;
            color: v.$blanco;
        }
    }

    
}

.button-delete {
    width: 100%;
}

