@use 'base/variables' as v;
@use 'base/mixins' as m;

.list-products {
    display: grid;
    gap: 2rem;

    @include m.tablet {
        @include m.grid(2, 2rem);
    }

    .product {
        background-color: v.$blanco;
        padding: 3rem;
        border-radius: 1rem;
        transition-property: transform;
        transition-duration: 300ms;
        text-align: center;
        box-shadow: rgba(0, 0, 0, .12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;

        &.selected {
            background-color: v.$verde;
            p {
                color: v.$blanco;
            }
        }

        &:hover {
            transform: scale(1.05);
            cursor: pointer;
        }

        p {
            color: v.$negro;
            margin: 0 0 calc(v.$separacion / 4) 0;
        }

        .product-price {
            color: v.$verde;
            font-weight: v.$bold;
            font-size: 3rem;
            margin: 0;
        }
    }
}