@use 'base/variables' as v;
@use 'base/mixins' as m;

.container-all {
    margin: 5rem 0;

    @include m.tablet {
        @include m.grid(2, 1fr);
        column-gap: 5rem;
        align-items: center;
        
    }

    .container-res {
        text-align: center;
        border-radius: 1rem;
        background-color: v.$azul;
        overflow: hidden;
        background-color: transparent;

        .container-info {
            display: flex;
            justify-content: space-between;
            align-items: center;
            padding: 1rem;
            background-color: v.$negro;

            .name-res {
                font-weight: v.$bold;
                color: v.$verde;
            }
            .date-res {
                font-weight: v.$regular;
                color: v.$blanco;
            }
        }

        .info-res {
            background-color: v.$blanco;
            color: v.$negro;
            border-bottom-left-radius: 1rem;
            border-bottom-right-radius: 1rem;
            padding: 1rem;

            p {
                margin-top: 0;
                color: v.$negro;
            }

            .services-select {
                color: v.$azul;
                font-weight: v.$bold;
                font-size: 2.2rem;
                text-decoration: underline;
            }
            .ser-name {
                font-weight: v.$bold;
            }
            .ser-price {
                font-weight: v.$bold;
            }
            .price {
                color: v.$verde;
            }
            .prod-select {
                color: v.$azul;
                font-weight: v.$bold;
                font-size: 2.2rem;
                text-decoration: underline;
            }
            .total {
                font-weight: v.$black;
                color: v.$verde;
                text-decoration: underline;
            }
        }
    }
}
