@use 'variables' as v;
@use 'mixins' as m;

::-webkit-scrollbar {
    display: none; /* for Chrome, Safari, and Opera */
}

html {
    font-size: 62.5%;
    box-sizing: border-box;
    height: 100%;
}
body {
    min-height: 100%;
    @include m.tablet {
        height: 100%;
    }
    display: flex;
    flex-direction: column;
    background-color: v.$background-color;
    color: v.$blanco;
    font-family: v.$fuente_principal;
    font-size: 1.6rem;
}
body::-webkit-scrollbar {
    display: none; /* for Chrome, Safari, and Opera */
  }
*, *:before, *:after {
    box-sizing: inherit;
}
.contenedor {
    width: 95%;
    max-width: 1200px;
    margin: 0 auto;
}
a {
    text-decoration: none;
}

img {
    max-width: 100%;
}

.container-submit {
    display: flex;
    justify-content: flex-end;
    margin: 0;
}

.container-image {
    height: 50rem;
    width: 50rem;
}

.name-container {
    background: #1e1e24;
    box-shadow: 0 0.4px 0.4px rgb(0 0 0 / 11%), 0 1px 1px rgb(0 0 0 / 16%), 0 2.1px 2.1px rgb(0 0 0 / 20%), 0 4.4px 4.4px rgb(0 0 0 / 24%), 0 12px 12px rgb(0 0 0 / 35%);
    padding: 1rem;
    border-radius: 1rem;
    p {
        text-align: center;
    }
}