@use 'base/variables' as v;

.content-sum {
    p {
        margin-bottom: 1rem;
    }
    span {
        color: v.$azul;
        font-weight: v.$bold;
        font-size: 2.4rem;
    }
}

.service-container {
    padding: 1rem;
    margin-bottom: 2rem;
    box-shadow: 0 0.4px 0.4px rgb(0 0 0 / 11%), 0 1px 1px rgb(0 0 0 / 16%), 0 2.1px 2.1px rgb(0 0 0 / 20%), 0 4.4px 4.4px rgb(0 0 0 / 24%), 0 12px 12px rgb(0 0 0 / 35%);
    border-radius: 1rem;
}
.product-container {
    padding: 1rem;
    margin-bottom: 2rem;
    box-shadow: 0 0.4px 0.4px rgb(0 0 0 / 11%), 0 1px 1px rgb(0 0 0 / 16%), 0 2.1px 2.1px rgb(0 0 0 / 20%), 0 4.4px 4.4px rgb(0 0 0 / 24%), 0 12px 12px rgb(0 0 0 / 35%);
    border-radius: 1rem;
}