html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}

body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  background-color: #1E1E24;
  color: #FFFFFF;
  font-family: "Roboto Mono", monospace;
  font-size: 1.6rem;
}
@media (min-width: 768px) {
  body {
    height: 100%;
  }
}

body::-webkit-scrollbar {
  display: none;
  /* for Chrome, Safari, and Opera */
}

*, *:before, *:after {
  box-sizing: inherit;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

.container-submit {
  display: flex;
  justify-content: flex-end;
  margin: 0;
}

.container-image {
  height: 50rem;
  width: 50rem;
}

.name-container {
  background: #1e1e24;
  box-shadow: 0 0.4px 0.4px rgba(0, 0, 0, 0.11), 0 1px 1px rgba(0, 0, 0, 0.16), 0 2.1px 2.1px rgba(0, 0, 0, 0.2), 0 4.4px 4.4px rgba(0, 0, 0, 0.24), 0 12px 12px rgba(0, 0, 0, 0.35);
  padding: 1rem;
  border-radius: 1rem;
}
.name-container p {
  text-align: center;
}

h1, h2, h3 {
  font-family: "Roboto Mono", monospace;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
  color: #FFFFFF;
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  text-align: center;
}

p {
  color: #FFFFFF;
  font-size: 2rem;
}

.title {
  padding: 2rem 0;
  background-color: #FFBA08;
  box-shadow: rgba(255, 255, 255, 0.4) 0px 2px 4px, rgba(255, 255, 255, 0.3) 0px 7px 13px -3px, rgba(255, 255, 255, 0.2) 0px -3px 0px inset;
}
.title .web-title {
  font-family: "Lobster", cursive;
  font-size: 10rem;
  color: #1a1b15;
}
.title .copy {
  font-size: 3rem;
  color: #1a1b15;
}

.buttons {
  margin-top: 5rem;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  column-gap: 5rem;
}
.buttons .button-guest {
  margin-top: 5rem;
  grid-column: 1/3;
}

.text-home {
  margin-top: 10rem;
  text-align: center;
}
.text-home .remember {
  text-decoration: underline;
}

.container-title {
  padding: 2rem;
  box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px, rgba(0, 0, 0, 0.3) 0px 7px 13px -3px, rgba(0, 0, 0, 0.2) 0px -3px 0px inset;
  border-radius: 2rem;
  background-color: #370617;
  margin-bottom: 3rem;
}

.page-name {
  text-align: center;
  margin: 2.5rem 0;
  font-weight: 700;
}

.page-details {
  text-align: center;
}

@media (min-width: 768px) {
  .container-app {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    min-height: 100vh;
  }
}
.container-app .image {
  height: 25rem;
  background-image: url(https://www.publicdomainpictures.net/pictures/130000/velka/barber-shop-chair.jpg);
  background-size: cover;
  background-position: center center;
  filter: contrast(150%);
}
@media (min-width: 768px) {
  .container-app .image {
    height: auto;
  }
}
.container-app .app {
  width: 100%;
  margin: 0 auto;
  padding: 2rem 2rem;
}
@media (min-width: 768px) {
  .container-app .app {
    overflow: scroll;
  }
}

.bar {
  margin: 5rem 0;
  text-align: center;
}
@media (min-width: 768px) {
  .bar {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.bar .boton {
  display: flex;
  justify-content: center;
  background-color: #1a1b15;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  align-items: center;
  width: 100%;
  border-radius: 1rem;
  margin: 0;
}
@media (min-width: 768px) {
  .bar .boton {
    width: auto;
  }
}
.bar .boton:hover {
  background-color: black;
  cursor: pointer;
}
.bar a {
  color: #FFFFFF;
}
.bar .container-user {
  margin: 2rem 0;
}
.bar .container-user .icon-user {
  font-size: 4rem;
  transition: color 300ms ease-in-out;
}
.bar .container-user .icon-user:hover {
  color: #00A676;
}

.services-bar {
  margin-bottom: 1.25rem;
}
@media (min-width: 768px) {
  .services-bar {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(2, 1fr);
  }
  .services-bar .boton {
    margin: 0;
  }
  .services-bar a:first-child {
    grid-column: 1/3;
  }
}

.user-bar {
  margin-top: 1.25rem;
  margin-bottom: 5rem;
}
@media (min-width: 768px) {
  .user-bar {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(3, 1fr);
  }
  .user-bar .boton {
    margin: 0;
  }
}
.user-bar .button-crud {
  margin-bottom: 2rem;
}

.section {
  padding: 5rem 0;
  display: none;
  box-shadow: rgba(60, 64, 67, 0.3) 0px 1px 2px 0px, rgba(60, 64, 67, 0.15) 0px 1px 3px 1px;
  padding: 2rem;
  border-radius: 1rem;
  margin-top: 2rem;
}
.section .text-align {
  text-align: center;
}

.show-section {
  display: block;
}

.show-comments {
  display: block;
}

.hide-section {
  display: none;
}

.text-align {
  text-align: center;
}

.list-services {
  display: grid;
  gap: 2rem;
}
@media (min-width: 768px) {
  .list-services {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.list-services .service {
  background-color: #FFFFFF;
  padding: 3rem;
  border-radius: 1rem;
  transition-property: transform;
  transition-duration: 300ms;
  text-align: center;
  box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
}
.list-services .service.selected {
  background-color: #00916E;
}
.list-services .service.selected p {
  color: #FFFFFF;
}
.list-services .service:hover {
  transform: scale(1.05);
  cursor: pointer;
}
.list-services .service p {
  color: #1a1b15;
  margin: 0 0 1.25rem 0;
}
.list-services .service .service-price {
  color: #00916E;
  font-weight: 700;
  font-size: 3rem;
  margin: 0;
}

.list-products {
  display: grid;
  gap: 2rem;
}
@media (min-width: 768px) {
  .list-products {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.list-products .product {
  background-color: #FFFFFF;
  padding: 3rem;
  border-radius: 1rem;
  transition-property: transform;
  transition-duration: 300ms;
  text-align: center;
  box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
}
.list-products .product.selected {
  background-color: #00916E;
}
.list-products .product.selected p {
  color: #FFFFFF;
}
.list-products .product:hover {
  transform: scale(1.05);
  cursor: pointer;
}
.list-products .product p {
  color: #1a1b15;
  margin: 0 0 1.25rem 0;
}
.list-products .product .product-price {
  color: #00916E;
  font-weight: 700;
  font-size: 3rem;
  margin: 0;
}

.list-comments {
  display: grid;
  gap: 2rem;
}
@media (min-width: 768px) {
  .list-comments {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
.list-comments .container-div {
  transition-property: transform;
  transition-duration: 300ms;
  border-radius: 1rem;
  background-color: transparent;
  overflow: hidden;
  border: none;
}
.list-comments .container-div:hover {
  transform: scale(1.05);
  cursor: pointer;
}
.list-comments .container-div .container-info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #30343F;
  padding: 0.5rem 1rem;
}
.list-comments .container-div .container-info .user-name-comment {
  font-weight: 700;
  color: #969696;
}
.list-comments .container-div .container-info .date-comment {
  font-weight: 300;
  color: #FFFFFF;
}
.list-comments .container-div .comment {
  text-align: center;
  padding: 1rem;
  background-color: #FFFFFF;
  border-bottom-left-radius: 1rem;
  border-bottom-right-radius: 1rem;
}
.list-comments .container-div .comment .comment-text {
  font-weight: 400;
  color: #1a1b15;
}
.list-comments .container-div .comment p {
  margin: 0 0 1.25rem 0;
}

.user-comments .container-all {
  margin-top: 5rem;
}
@media (min-width: 768px) {
  .user-comments .container-all {
    display: grid;
    gap: 5rem;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
  }
}
.user-comments .container-all .container-buttons {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  margin-top: 2rem;
}
@media (min-width: 768px) {
  .user-comments .container-all .container-buttons {
    margin-top: 0;
  }
}
.user-comments .container-all .boton {
  margin: 0;
}
.user-comments .container-all .container-div {
  transition-property: transform;
  transition-duration: 300ms;
  border-radius: 1rem;
  background-color: transparent;
  overflow: hidden;
  border: none;
}
.user-comments .container-all .container-div:hover {
  transform: scale(1.05);
  cursor: pointer;
}
.user-comments .container-all .container-div .container-info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #30343F;
  padding: 0.5rem 1rem;
}
.user-comments .container-all .container-div .container-info .user-name-comment {
  font-weight: 700;
  color: #969696;
}
.user-comments .container-all .container-div .container-info .date-comment {
  font-weight: 300;
  color: #FFFFFF;
}
.user-comments .container-all .container-div .comment {
  text-align: center;
  padding: 1rem;
  background-color: #FFFFFF;
  border-bottom-left-radius: 1rem;
  border-bottom-right-radius: 1rem;
}
.user-comments .container-all .container-div .comment .comment-text {
  font-weight: 400;
  color: #1a1b15;
}
.user-comments .container-all .container-div .comment p {
  margin: 0 0 1.25rem 0;
}

.content-sum p {
  margin-bottom: 1rem;
}
.content-sum span {
  color: #436b84;
  font-weight: 700;
  font-size: 2.4rem;
}

.service-container {
  padding: 1rem;
  margin-bottom: 2rem;
  box-shadow: 0 0.4px 0.4px rgba(0, 0, 0, 0.11), 0 1px 1px rgba(0, 0, 0, 0.16), 0 2.1px 2.1px rgba(0, 0, 0, 0.2), 0 4.4px 4.4px rgba(0, 0, 0, 0.24), 0 12px 12px rgba(0, 0, 0, 0.35);
  border-radius: 1rem;
}

.product-container {
  padding: 1rem;
  margin-bottom: 2rem;
  box-shadow: 0 0.4px 0.4px rgba(0, 0, 0, 0.11), 0 1px 1px rgba(0, 0, 0, 0.16), 0 2.1px 2.1px rgba(0, 0, 0, 0.2), 0 4.4px 4.4px rgba(0, 0, 0, 0.24), 0 12px 12px rgba(0, 0, 0, 0.35);
  border-radius: 1rem;
}

.reservations, .services {
  list-style: none;
  padding: 0;
}
.reservations li, .services li {
  padding: 1rem;
  border-top: 1px solid #FFFFFF;
}
.reservations li p, .services li p {
  font-weight: 700;
  color: #436b84;
}
.reservations li span, .services li span,
.reservations li .service,
.services li .service {
  font-weight: 400;
  color: #FFFFFF;
}

.search {
  margin-bottom: 5rem;
}

.container-all {
  margin: 5rem 0;
}
@media (min-width: 768px) {
  .container-all {
    display: grid;
    gap: 1fr;
    grid-template-columns: repeat(2, 1fr);
    column-gap: 5rem;
    align-items: center;
  }
}
.container-all .container-res {
  text-align: center;
  border-radius: 1rem;
  background-color: #436b84;
  overflow: hidden;
  background-color: transparent;
}
.container-all .container-res .container-info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
  background-color: #1a1b15;
}
.container-all .container-res .container-info .name-res {
  font-weight: 700;
  color: #00916E;
}
.container-all .container-res .container-info .date-res {
  font-weight: 400;
  color: #FFFFFF;
}
.container-all .container-res .info-res {
  background-color: #FFFFFF;
  color: #1a1b15;
  border-bottom-left-radius: 1rem;
  border-bottom-right-radius: 1rem;
  padding: 1rem;
}
.container-all .container-res .info-res p {
  margin-top: 0;
  color: #1a1b15;
}
.container-all .container-res .info-res .services-select {
  color: #436b84;
  font-weight: 700;
  font-size: 2.2rem;
  text-decoration: underline;
}
.container-all .container-res .info-res .ser-name {
  font-weight: 700;
}
.container-all .container-res .info-res .ser-price {
  font-weight: 700;
}
.container-all .container-res .info-res .price {
  color: #00916E;
}
.container-all .container-res .info-res .prod-select {
  color: #436b84;
  font-weight: 700;
  font-size: 2.2rem;
  text-decoration: underline;
}
.container-all .container-res .info-res .total {
  font-weight: 900;
  color: #00916E;
  text-decoration: underline;
}

.form {
  width: 100%;
  margin-top: 5rem;
  align-items: center;
  padding: 2rem 4rem 5rem 4rem;
  color: white;
  background: #1E1E24;
  border-radius: 10px;
  box-shadow: 0 0.4px 0.4px rgba(0, 0, 0, 0.109), 0 1px 1px rgba(0, 0, 0, 0.155), 0 2.1px 2.1px rgba(0, 0, 0, 0.195), 0 4.4px 4.4px rgba(0, 0, 0, 0.241), 0 12px 12px rgba(0, 0, 0, 0.35);
}

.field {
  display: flex;
  margin-bottom: 2rem;
  flex-direction: column;
  padding: 1rem;
  border-radius: 1.5rem;
  box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
}
.field label {
  margin-bottom: 1rem;
}
.field input {
  border: none;
  padding: 1.25rem;
  border-radius: 1rem;
}
.field input:disabled {
  background-color: #cfd1c4;
  color: #1a1b15;
  cursor: not-allowed;
}

.boton {
  display: flex;
  justify-content: center;
  background-color: #f48c06;
  padding: 1.5rem 4rem;
  color: #FFFFFF;
  margin-top: 2rem;
  font-size: 2rem;
  font-weight: 400;
  font-weight: 700;
  border: none;
  transition-property: background-color;
  transition-duration: 0.3s;
  text-align: center;
  align-items: center;
  width: 100%;
  border-radius: 1rem;
}
@media (min-width: 768px) {
  .boton {
    width: auto;
  }
