:root {
  --underline-height: .5em;
  --transition-duration: .5s;
}

nav {
  position: relative;
  white-space: nowrap;
  background: transparent;
  padding: var(--underline-height) 0;
  box-shadow: 0 1em 2em rgba(0, 0, 0, 0.05);
}

.underline {
  display: block;
  position: absolute;
  z-index: 0;
  bottom: 0;
  left: 0;
  height: var(--underline-height);
  width: 20%;
  background: black;
  pointer-events: none;
  mix-blend-mode: multiply;
  transition: transform var(--transition-duration) ease-in-out;
}
.underline:nth-child(1) {
  background: yellow;
  transition: calc(var(--transition-duration) * 0.8);
}
.underline:nth-child(2) {
  background: cyan;
  transition: calc(var(--transition-duration) * 1.2);
}
.underline:nth-child(3) {
  background: magenta;
}

nav a {
  display: block;
  z-index: 10;
  width: 20%;
  padding: 1em 0;
  text-align: center;
  cursor: pointer;
  font-size: 2rem;
  transition: color 300ms ease-in-out;
}

nav a:hover {
  color: #F7DC6F;
}

@media (min-width: 768px) {
  nav.tabs {
    display: flex;
  }
}
nav.tabs .underline {
  background: #F7DC6F;
  border-radius: 1em;
  height: calc(var(--underline-height) / 2);
  mix-blend-mode: initial;
}

nav.full {
  font-weight: bold;
  background: #111;
  color: white;
}
nav.full .underline {
  height: 100%;
  background: gold;
}

nav.retro .underline {
  border-radius: 100% 100% 0 0;
}
nav.retro .underline:nth-child(1) {
  background: gold;
}
nav.retro .underline:nth-child(2) {
  background: dodgerblue;
}
nav.retro .underline:nth-child(3) {
  background: tomato;
}

.pagination {
  padding: 5rem 0;
}
@media (min-width: 768px) {
  .pagination {
    display: flex;
    justify-content: space-between;
  }
}
.pagination .icon-pager {
  font-size: 2.4rem;
}

.hide {
  visibility: hidden;
}