@use 'base/variables' as v;
@use 'base/mixins' as m;

.container-app {
    @include m.tablet {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        min-height: 100vh;
    }

    .image {
        height: 25rem;
        background-image: url(https://www.publicdomainpictures.net/pictures/130000/velka/barber-shop-chair.jpg);
        background-size: cover;
        background-position: center center;
        filter: contrast(150%);

        @include m.tablet {
            height: auto;
        }
    }

    .app {
        width: 100%;
        margin: 0 auto;
        padding: 2rem 2rem;
        @include m.tablet {
           overflow: scroll;
        }
    }
}

.bar {
    margin: v.$separacion 0;
    text-align: center;

    @include m.tablet {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    .boton {
        @include m.boton(v.$negro, v.$blanco);
        margin: 0;
    }
    a {
        color: v.$blanco;
    } 
    .container-user {
        margin: 2rem 0;
        .icon-user {
            font-size: 4rem;
            transition: color 300ms ease-in-out;
    
            &:hover {
                color: v.$button-pager;
            }
        }
    }
}

.services-bar {
    margin-bottom: v.$separacion / 4;
    @include m.tablet {
        @include m.grid(2, 1rem);
        .boton {
            margin: 0;
        }
        a:first-child {
            grid-column: 1 / 3;
        }
    }
}

.user-bar {
    margin-top: v.$separacion / 4;
    margin-bottom: v.$separacion;
    @include m.tablet {
        @include m.grid(3, 1rem);
        .boton {
            margin: 0;
        }
    }
    .button-crud {
        margin-bottom: 2rem;
    }
}