@use 'base/variables' as v;
@use 'base/mixins' as m;

.form {
    width: 100%;
    margin-top: 5rem;
    align-items: center;
    padding: 2rem 4rem 5rem 4rem;
    color: white;
    background: #1E1E24;
    border-radius: 10px;
    box-shadow: 0 0.4px 0.4px rgba(0, 0, 0, 0.109),
        0 1px 1px rgba(0, 0, 0, 0.155),
        0 2.1px 2.1px rgba(0, 0, 0, 0.195),
        0 4.4px 4.4px rgba(0, 0, 0, 0.241),
        0 12px 12px rgba(0, 0, 0, 0.35);
}

.field {
    display: flex;
    margin-bottom: 2rem;
    flex-direction: column;
    padding: 1rem;
    border-radius: 1.5rem;
    box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
    label {
        margin-bottom: 1rem;
    }

    input {
        border: none;
        padding: calc(v.$separacion / 4);
        border-radius: 1rem;

        &:disabled {
            background-color: lighten(v.$negro, 70%);
            color: v.$negro;
            cursor: not-allowed;
        }
    }
}


