:root {
	--underline-height: .5em;
	--transition-duration : .5s;
}

nav {
	position: relative;
	white-space: nowrap;
	background: transparent;
	padding: var(--underline-height) 0;
	box-shadow: 0 1em 2em rgba(black, .05);
}

.underline {
	display: block;
	position: absolute;
	z-index: 0;
	bottom: 0;
	left: 0;
	height: var(--underline-height);
	width: 20%;
	background: black;
	pointer-events: none;
	mix-blend-mode: multiply;
	transition: transform var(--transition-duration) ease-in-out;
	
	&:nth-child(1) {
		background: yellow;
		transition: calc(var(--transition-duration) * .8);
	}
	&:nth-child(2) {
		background: cyan;
		transition: calc(var(--transition-duration) * 1.2);
	}
	&:nth-child(3) {
		background: magenta;
	}
}

nav a {
	display: block;
	z-index: 10;
	width: 20%;
	padding: 1em 0;
	text-align: center;
	cursor: pointer;
    font-size: 2rem;
    transition: color 300ms ease-in-out;
}
nav a:hover {
    color: v.$yellow;
}

nav.tabs {
    @include m.tablet {
        display: flex;
    }
	.underline {
		background: v.$yellow;
		border-radius: 1em;
		height: calc(var(--underline-height) / 2);
		mix-blend-mode: initial;
	}
}

nav.full {
	font-weight: bold;
	background: #111;
	color: white;
	
	.underline {
		height: 100%;
		background: gold;
	}
}

nav.retro {
	.underline {	
		border-radius: 100% 100% 0 0;
		
		&:nth-child(1) { background: gold; }
		&:nth-child(2) { background: dodgerblue; }
		&:nth-child(3) { background: tomato; }
	}
}